import {COLLAPSED} from "../actions/actionTypes"

let initCollapsed = {
    collapsed:null
}

export default function (state=initCollapsed,action) {
    switch (action.type){
        case COLLAPSED:
            return !action.collapsed
        default:
            return initCollapsed
    }
}